.trackerBlock {
    box-sizing: border-box;
    padding: 20px;
    width: 100%;
    max-width: 350px;
    margin: 100px auto;
    display: table;
    
}

.welcome {
    color: rgba(255, 255, 255, 0.9);
    display: flex;
    justify-content: space-between;
    margin: 0;
    text-shadow: 0px 5px 3px #292929;
}
.totalMoney {
    color: rgba(255, 255, 255, 0.9);
    font-size: 20px;
    margin:0 0 10px;
    text-shadow: 0px 5px 3px #292929;
}

.newTransaction {
    box-sizing: border-box;
    text-align: center;
    color: #425f9c;
    display: table;
    width: 100%;
    border-radius: 10px;
    background: white;
    background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.8),
    rgba(255, 255, 255, 0.3)
  );
    border: 0;
    padding: 10px;
    margin:0 0 10px;
    font-size: 15px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.newTransaction input[type="text"],
.newTransaction select{
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgb(186, 227, 238);
    font-size: 13px;
    padding: 7px;
    border-radius: 5px;
    background: white;
    background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.8),
    rgba(255, 255, 255, 0.3)
  );
}

.newTransaction input[type="text"]:focus,
.newTransaction select:focus{
    outline: none;
}

.inputGroup {
    display:flex;
    justify-content: center;
}

.addTransaction {
    text-align: center;
    color: #b6c2ec;
    border-radius: 5px;
    background: linear-gradient(to right top, #011d79, #4d58f5);
    border: 0;
    margin: 5px 0 0;
    padding: 5px 10px;
    font-size: 15px;
    cursor: pointer;
}

.addTransaction:hover {
    color: #ffffff;
    background: linear-gradient(to right top, #011d79, #434cce);

}

.addTransaction:focus{
    outline: none;
}



.latestTransactions {
    margin: 10px 0 0 0;
    background: white;
    background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.8),
    rgba(255, 255, 255, 0.3)
  );
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.latestTransactions p{
    font-weight: bold;
    padding: 0 0 5px;
    margin: 0;
    font-size: 13px;
    border: 1px solid rgb(186, 227, 238);
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

ul li {
    border: 1px solid rgb(186, 227, 238);
    padding: 5px 0;
    display: flex;
    justify-content: space-between;
}

ul li:last-child{
    border-bottom: 0;
    padding: 5px 0 0;
}

.expense {
    color: red;
}

.deposit {
    color: green;
}

.exit {
    background: white;
    background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.8),
    rgba(255, 255, 255, 0.3)
  );
    border: 0;
    border-radius: 3px;
    color: red;
    cursor: pointer;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.exit:hover {
    background-color: #f1f1f1;
}